import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { parse } from 'papaparse';
import { BrowserRouter as Router, Route, Routes } from 'react-router-dom'; 
import TitleBar from './Compon/TitleBar/TitleBar';
import PageButtons from './Compon/PageButton/PageButtons';

import Home from './Compon/Pages/Home/Home';
import MakeWay from './Compon/Pages/MakeWay/MakeWay'; 
import OWData from './Compon/Pages/OWData/OWData';
import './App.css';

function App() {
  const [data, setData] = useState([]);
  const [refreshTime, setRefreshTime] = useState(120);

  const fetchData = async () => {
    const sheetUrl = 'https://docs.google.com/spreadsheets/d/e/2PACX-1vRZ88nEq5vzLNdVE68AR5kMzS5HveQgQUxXF9rNX-9yhmv2jf2Bohwja3j-vDzUcGbO2ziCZ-Mv3v4J/pub?gid=1125870162&single=true&output=csv';
  
    try {
      const response = await axios.get(sheetUrl);
      const parsedData = parse(response.data, { header: true });
      setData(parsedData.data);
      setRefreshTime(120);
    } catch (error) {
      console.error('Error fetching data:', error);
    }
  };

  const handleRefresh = () => {
    fetchData();
  };

  useEffect(() => {
    fetchData();

    const intervalId = setInterval(() => {
      fetchData();
    }, 120000);

    const timerInterval = setInterval(() => {
      setRefreshTime((prevTime) => prevTime - 1);
    }, 1000);

    return () => {
      clearInterval(intervalId);
      clearInterval(timerInterval);
    };
  }, []);

  return (
    <Router >
      <div className="page">
        <TitleBar refreshTime={refreshTime} onTimeout={handleRefresh} />
        <div className='buttonHolder'>
          <PageButtons />
        </div>
        <Routes>
          <Route path="/MakeWayData" element={<Home />} />
          <Route path="/MakeWayData/Home/MakeWayData" element={<MakeWay />} />
          <Route path="/MakeWayData/Home/OWData" element={<OWData />} />
        </Routes>
      </div>
    </Router>
  );
}

export default App;