import React, { useState, useEffect } from 'react';
import axios from 'axios';

import './OWData.css';

function Page2() {
  const [overwatchStats, setOverwatchStats] = useState(null);
  const gamerTag = 'HsimanDitto-1991'; // Update with your gamer tag

  useEffect(() => {
    const fetchOverwatchStats = async () => {
      try {
        const accessToken = 'USOzgb1SGiE4CZnw58JQwu3jsnY8Ul1H8m'; // Extract the access_token from your API key
        const response = await axios.get(`https://api.blizzard.com/profile/wow/character/${gamerTag}/statistics?namespace=profile-us&locale=en_US&access_token=${accessToken}`);
        
        // Update the state with the received Overwatch stats data
        setOverwatchStats(response.data);
      } catch (error) {
        console.error('Error fetching Overwatch stats:', error);
      }
    };

    fetchOverwatchStats();
  }, [gamerTag]);

  return (
    <div className='page'>
      <div className="pageHolder">
        {overwatchStats ? (
          <div>
            {/* Display the Overwatch stats data here */}
            {/* Example: <p>Wins: {overwatchStats.wins}</p> */}
          </div>
        ) : (
          <p>Loading Overwatch stats...</p>
        )}
      </div>
    </div>
  );
}

export default Page2;