import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { parse } from 'papaparse';
import BarChart from '../Charts/BarChart/BarChart';
import LineChart from '../Charts/LineChart/LineChart';
import PieChart from '../Charts/PieChart/PieChart';
import Doughnut from '../Charts/DougnutChart/DougnutChart';
import StatOptions from '../StatOptions/StatOptions';
import './ChartSelection.css';


function ChartSelection() {
  const [selectedChart, setSelectedChart] = useState('Bar Chart');
  const [selectedStatOptions, setSelectedStatOptions] = useState(['First']); 
  const [selectedPlayerOption, setSelectedPlayerOption] = useState(['nulls']); 
  const [data, setData] = useState([]);

  const handleChartChange = (event) => {
    setSelectedChart(event.target.value);
  };

  const handleStatOptionChange = (event) => {
    const option = event.target.value;
    if (selectedStatOptions.includes(option)) {
    
      setSelectedStatOptions(selectedStatOptions.filter((item) => item !== option));
    } else {
      
      setSelectedStatOptions([option]);
    }
  };

  const handlePlayerOptionChange = (event) => {
    const option = event.target.value;

    if (selectedPlayerOption.includes(option)) {
    
      setSelectedPlayerOption(selectedPlayerOption.filter((item) => item !== option));
    } else {
      
      setSelectedPlayerOption([...selectedPlayerOption, option]);
    }
  };

  useEffect(() => {
   
    const sheetUrl =
      'https://docs.google.com/spreadsheets/d/e/2PACX-1vRZ88nEq5vzLNdVE68AR5kMzS5HveQgQUxXF9rNX-9yhmv2jf2Bohwja3j-vDzUcGbO2ziCZ-Mv3v4J/pub?gid=1125870162&single=true&output=csv';
    axios
      .get(sheetUrl)
      .then((response) => {
        const parsedData = parse(response.data, { header: true });
        setData(parsedData.data);
      })
      .catch((error) => {
        console.error(error);
      });
  }, [selectedChart]);

  return (
    <div className='charts-selection'>
      <div className='left-area'>
        <div className='chart-selector'>
          <label>Select a Chart:</label>
          <select value={selectedChart} onChange={handleChartChange}>
            <option value='Bar Chart'>Bar Chart</option>
            <option value='Line Chart'>Line Chart</option>
            <option value='Pie Chart'>Pie Chart</option>
            <option value='Doughnut Chart'>Doughnut Chart</option>
          </select>
        </div>
        <div className='stat-options-area'>
          <div className='main-stat-options'>
            <StatOptions selectedStatOptions={selectedStatOptions} handleStatOptionChange={handleStatOptionChange} />
          </div>
        {/*
          
          <div className='Compair'></div>

          <div className='alt-stat-options'>
             <PlayerOptions selectedPlayerOption={selectedPlayerOption} handlePlayerOptionChange={handlePlayerOptionChange} />
          </div>
          
          */}
        </div>
      </div>
      <div className='right-chart'>
        {selectedChart === 'Bar Chart' && (
          <BarChart data={data} selectedStatOptions={selectedStatOptions} selectedPlayerOption={selectedPlayerOption} />
        )}
        {selectedChart === 'Line Chart' && (
          <LineChart data={data} selectedStatOptions={selectedStatOptions} selectedPlayerOption={selectedPlayerOption} />
        )}
        {selectedChart === 'Pie Chart' && (
          <PieChart data={data} selectedStatOptions={selectedStatOptions} selectedPlayerOption={selectedPlayerOption} />
        )}
        {selectedChart === 'Doughnut Chart' && (
          <Doughnut data={data} selectedStatOptions={selectedStatOptions} selectedPlayerOption={selectedPlayerOption} />
        )}
      </div>
    </div>
  );
}

export default ChartSelection;
