import React from 'react';
import './StatOptions.css';

function StatOptions({ selectedStatOptions, handleStatOptionChange }) {
  return (
    <div className='stat-options'>
      <label>Select Stat Options:</label>
      <div>
        <label>
          <input
            type='checkbox'
            value='First'
            checked={selectedStatOptions.includes('First')}
            onChange={handleStatOptionChange}

            />
            First Wins
          </label>
          <label>
            <input
              type='checkbox'
              value='Second'
              checked={selectedStatOptions.includes('Second')}
              onChange={handleStatOptionChange}
            />
            Second Wins
          </label>
          <label>
            <input
              type='checkbox'
              value='Third'
              checked={selectedStatOptions.includes('Third')}
              onChange={handleStatOptionChange}
            />
            Third Wins
          </label>
          <label>
            <input
              type='checkbox'
              value='Fourth'
              checked={selectedStatOptions.includes('Fourth')}
              onChange={handleStatOptionChange}
            />
            Fourth Wins
          </label>
          <label>
            <input
              type='checkbox'
              value='O/T Wins'
              checked={selectedStatOptions.includes('O/T Wins')}
              onChange={handleStatOptionChange}
            />
            O/T Wins
          </label>
          <label>
            <input
              type='checkbox'
              value='O/T Loss'
              checked={selectedStatOptions.includes('O/T Loss')}
              onChange={handleStatOptionChange}
            />
            O/T Losses
          </label>
          <label>
            <input
              type='checkbox'
              value='O/T Games'
              checked={selectedStatOptions.includes('O/T Games')}
              onChange={handleStatOptionChange}
            />
            O/T Games
          </label>
          <label>
            <input
              type='checkbox'
              value='TotalPoints'
              checked={selectedStatOptions.includes('TotalPoints')}
              onChange={handleStatOptionChange}
            />
            Total Points
          </label>
          <label>
            <input
              type='checkbox'
              value='PointsPerGame'
              checked={selectedStatOptions.includes('PointsPerGame')}
              onChange={handleStatOptionChange}
            />
            Points Per Game
          </label>
        </div>
      </div>
  );
}

export default StatOptions;
