import React from 'react';
import { Link } from 'react-router-dom';
import './PageButtons.css';

function PageButtons() {
  return (
    <div className="button-bar">
      <Link to="MakeWayData">
        <button className="button">Home</button>
      </Link>
      <Link to="MakeWayData/Home/MakeWayData">
        <button className="button">MakeWay Stat's</button>
      </Link>
      <Link to="MakeWayData/Home/OWData">
        <button className="button">O/W Stat's</button>
      </Link>
      <Link to="MakeWayData/Home/page3">
        <button className="button">Button 4</button>
      </Link>
      <Link to="MakeWayData/Home/Page4">
        <button className="button">Button 5</button>
      </Link>
    </div>
  );
}

export default PageButtons;