import React from 'react';
import PropTypes from 'prop-types';
import './PlayerCard.css';

const PlayerCard = ({ player }) => {
  if (!player) {
    return null;
  }

  const stats = Object.keys(player)
    .filter(key => key !== 'PlayerName' && key !== 'FavCar' && key !== 'TotalPoints' && key !== 'ProfileIMG' && key !== 'PlayerColor');
  const borderColors = stats.map((stat) => hexToRgba(player.PlayerColor, 0.5));

  return (
    <div className="baseball-card" style={{ backgroundColor: borderColors }}>
      <div className="player-info">
        <h2 className="player-name">{player.PlayerName}</h2>
        <img src={player.ProfileIMG} alt="Player" className="player-img" />
        <p className="player-fav-car">Fav Car: {player.FavCar}</p>
        <div className="player-stats">
          {stats.map((stat, index) => (
            <p key={stat} style={{ /* Apply your styles here */ }}>
              {stat}: {player[stat]}
            </p>
          ))}
        </div>
      </div>
    </div>
  );
};

PlayerCard.propTypes = {
  player: PropTypes.shape({
    PlayerName: PropTypes.string.isRequired,
    FavCar: PropTypes.string.isRequired,
    FirstPlace: PropTypes.number.isRequired,
    SecondPlace: PropTypes.number.isRequired,
    ThirdPlace: PropTypes.number.isRequired,
    FourthPlace: PropTypes.number.isRequired,
    OTWins: PropTypes.number.isRequired,
    OTLoss: PropTypes.number.isRequired,
    OTGames: PropTypes.number.isRequired,
    TotalPoints: PropTypes.number.isRequired,
  }),
};

function hexToRgba(hex, alpha) {
  if (hex) {
    hex = hex.replace(/^#/, '');
    const r = parseInt(hex.slice(0, 2), 16);
    const g = parseInt(hex.slice(2, 4), 16);
    const b = parseInt(hex.slice(4, 6), 16);
    return `rgba(${r}, ${g}, ${b}, ${alpha})`;
  } else {
    // Provide a default color in case PlayerColor is undefined
    return `rgba(0, 0, 0, ${alpha})`;
  }
}

export default PlayerCard;