import React, { useEffect, useRef } from 'react';
import Chart from 'chart.js/auto';
import './BarChart.css';

function BarChart({ data, selectedStatOptions }) { 
  const chartRef = useRef(null);
  const chartInstance = useRef(null);

  useEffect(() => {
    if (data && data.length > 0) {
      
      if (chartInstance.current) {
        chartInstance.current.destroy();
      }

      const ctx = chartRef.current.getContext('2d');
      const borderColors = data.map((item) => hexToRgba(item.PlayerColor, 0.7));
      const glowColors = data.map((item) => hexToRgba(item.PlayerColor, 0.8));

      
      const statData = data.map((item) => {
        const values = selectedStatOptions.map((option) => {
          switch (option) {
            case 'First':
              return item.FirstPlace;
            case 'Second':
              return item.SecondPlace;
            case 'Third':
              return item.ThirdPlace;
            case 'Fourth':
              return item.FourthPlace;
            case 'O/T Wins':
              return item.OTWins;
            case 'O/T Loss':
              return item.OTLoss;
            case 'O/T Games':
              return item.OTGames;
              case 'TotalPoints':
              return item.TotalPoints;
              case 'PointsPerGame':
                return item.PointsPerGame;
            default:
              return 0;
          }
        });
        return values.reduce((acc, value) => acc + value, 0);
      });

      const datasetLabels = selectedStatOptions.map((option) => `${option} Wins`);
      

      chartInstance.current = new Chart(ctx, {
        type: 'bar',
        data: {
          labels: data.map((item) => item.PlayerName),
          datasets: [
            {
              data: statData,
              label: 'Bars',
              backgroundColor: glowColors,
              borderColor: borderColors,
              borderWidth: 4,
              type: 'bar',
              hidden: false,
            },
          ],
        },
        options: {
          responsive: true,
          scales: {
            x: {
              ticks: {
                color: 'rgba(255, 255, 255, 0.8)',
              },
              grid: {
                color: 'white',
              },
            },
            y: {
              ticks: {
                color: 'white',
              },
              grid: {
                color: 'white',
              },
            },
          },
          plugins: {
            title: {
              display: true,
              text: datasetLabels.join(', '), 
              label: 'Bar Chart',
              color: 'white',
              font: {
                size: 16,
              },
            },
            legend: {
              position: 'top',
              display: true,
              labels: {
                text: 'asdasd',
                color: 'white',
              },
            },
            tooltip: {
              enabled: true,
              callbacks: {
                title: () => {
                  return datasetLabels;
                },
                label: (context) => {
                  const value = context.parsed.y;
                  return `${value} ${selectedStatOptions[context.dataIndex]}`;
                },
              },
            },
          },
        },
      });
    }
  }, [data, selectedStatOptions]);

  function hexToRgba(hex, alpha) {
    if (hex) {
      hex = hex.replace(/^#/, '');
      const r = parseInt(hex.slice(0, 2), 16);
      const g = parseInt(hex.slice(2, 4), 16);
      const b = parseInt(hex.slice(4, 6), 16);
      return `rgba(${r}, ${g}, ${b}, ${alpha})`;
    } else {
      
      return `rgba(0, 0, 0, ${alpha})`;
    }
  }

  return <canvas ref={chartRef} id="BarChart" width="100%" height="100%" />;
}

export default BarChart;